import { Component, EventEmitter, Output } from '@angular/core';
import { BillpayPayeeDataService } from '../../services/billpay-payee-data.service';

@Component({
  selector: 'bb-billpay-ebills-delete-modal',
  templateUrl: './billpay-ebills-delete-modal.component.html',
})
export class BillpayEbillsDeleteModalComponent {
  @Output() resolve = new EventEmitter<boolean>();

  get selectedDeleteEbills$() {
    return this.payeeListWidgetService.selectedDeleteEbills;
  }

  /** @internal */
  constructor(private readonly payeeListWidgetService: BillpayPayeeDataService) {}

  cancelModal() {
    this.resolve.emit(false);
  }

  confirmModal() {
    this.resolve.emit(true);
  }
}
